@tailwind base;
@tailwind components;
@tailwind utilities;

*::-webkit-scrollbar { display: none; }
body { 
    background-image: url('./assets/bigsur.jpg'); 
    background-size: cover;
}

.type {
    position: relative;
}

.type::after {
    content: '';
    position: absolute;
    height: 80%;
    width: .5rem;
    top: 50%;
    transform: translateY(-50%);
    background-color: rgba(255, 255, 255, .7);
    animation: cursor 1.1s infinite step-start;
    animation-delay: .7s;
}

@keyframes cursor {
    0% { opacity: 0 }
    100% { opacity: 0 }
}